body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-bevan: Bevan;
  --font-khand: Khand;
  --font-inter: Inter;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;
  --font-size-27xl: 46px;
  --font-size-lg: 18px;

  /* Colors */
  --white: #fff;
  --red: #c92b1e;
  --body-text: #232329;
  --dark-brown: #3a0a04;
  --yellow: #f8d147;
  --color-black: #000;
  --color-gainsboro: #d9d9d9;

  /* Gaps */
  --gap-5xl: 24px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-mini: 15px;
  --padding-37xl: 56px;
  --padding-5xl: 24px;

  /* border radiuses */
  --br-9xl: 28px;
  --br-base: 16px;
}
